:root {
	--gutter: 20px;
}


.grid_contain {
	width: 100%;
	display: flex;
	margin-top: 25px;

}
.main__container{
	width: 100%;
}
.banner__container {
	display: flex;
	justify-content: space-between;
	margin-bottom: 25px;
	height: 120px;
	position: relative;
}
.banner__item {
	background: var(--banner);
	border-radius: 4px;
	border: 1px solid var(--border);
	padding: 14px;
	position: relative;
	overflow: hidden;
	cursor: pointer;
	width: 50%;
	height: 120px;
	transition: background 0.2s ease, border 0.2s ease;
}
.banner__item {
	margin-left: 20px;
}
.banner__item:first-child {
	margin-left: 0;
}
.banner__item h3 {
	color: var(--banner-text);
	transition: color 0.2s ease;
	font-weight: 500;
	font-size: 14px;
}
.banner__item img {
	position: absolute;
	right: 0;
	bottom: 0;
	object-fit: cover;
	height: 100px;
	max-width: none;
	transition: transform 0.2s ease;
}
.banner__item:hover img {
	transform: scale(1.1);
	transition: transform 0.2s ease;
}
.module__header {
	display: flex;
	justify-content: space-between;
	margin-bottom: 25px;
}
.module__header h3 {
	color: var(--color-dark);
  transition: color 0.2s ease;
}
.products_grid {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(286px, 1fr));
	grid-gap: 20px;
}

.filters {
	width: 250px;
	margin-right: 20px;
	position: relative;
	width: 350px;
}
.filters .rr {
	position: sticky;
  top: 20px;

}

.filters h3 {
	color: var(--color-dark);
	transition: color 0.2s ease;
	margin-bottom: 20px;
}
.filters_price__input {
	width: 100%;
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type=number] {
	-moz-appearance: textfield;
}





.pagination__container {
	margin-top: 80px;
	margin-bottom: 60px;
	display: flex;
	justify-content: center;
}
.pagination__container span {
	margin-right: 20px;
	color: var(--color-dark);
	cursor: pointer;
	font-weight: 400;
	font-size: 18px;
}
.pagination__container span.active {
	color: var(--color-accent);
}
.pagination__container .pagination__next {
	color: var(--color-accent);
	cursor: pointer;
	font-weight: 500;
	font-size: 18px;
}







@media screen and (max-width: 500px) {
	.grid_contain {
		display: flex;
		flex-direction: column;
	}
	.banner__item {
		position: absolute;
		margin: 0;
		width: 100%;
	}
}
